<template>
	<view class="uni-container">
		<!-- 自定义导航栏 -->
		<view class="navBarBox">
			<!-- 状态栏占位 -->
			<view class="statusBar" :style="{ paddingTop: statusBarHeight + 'px' }"></view>
			<!-- 真正的导航栏内容 -->
			<view class="navBar">
				<image class="logo" src="/static/f1_logo.svg" mode="scaleToFill"></image>
				<view>留言反馈</view>
			</view>
		</view>

		<uni-forms ref="form" :value="formData" validate-trigger="submit" err-show-type="toast">
			<uni-forms-item name="content" label="反馈内容/留言内容" required>
				<textarea @input="binddata('content', $event.detail.value)" class="uni-textarea-border"
					v-model="formData.content" trim="right"></textarea>
			</uni-forms-item>
			<uni-forms-item name="imgs" label="图片列表">
				<uni-file-picker file-mediatype="image" :limit="6" return-type="array" v-model="formData.imgs">
				</uni-file-picker>
			</uni-forms-item>
			<uni-forms-item name="contact" label="联系人">
				<uni-easyinput v-model="formData.contact" trim="both"></uni-easyinput>
			</uni-forms-item>
			<uni-forms-item name="mobile" label="联系电话">
				<uni-easyinput v-model="formData.mobile" trim="both"></uni-easyinput>
			</uni-forms-item>
			<view class="uni-button-group">
				<button type="primary" class="uni-button" @click="submit">提交</button>
			</view>
		</uni-forms>

		<uni-fab v-if="uniIDHasRole('Manager') || uniIDHasRole('admin')" :pattern="pattern" ref="fab" horizontal="right"
			vertical="bottom" :pop-menu="false" @fabClick="fabClick" />
	</view>
</template>

<script>
	import {
		validator
	} from '../../js_sdk/validator/opendb-feedback.js';
	console.log(validator);
	const db = uniCloud.database();
	const dbCollectionName = 'opendb-feedback';

	function getValidator(fields) {
		let result = {}
		for (let key in validator) {
			if (fields.indexOf(key) > -1) {
				result[key] = validator[key]
			}
		}
		return result
	}

	export default {
		data() {
			let formData = {
				"content": "",
				"imgs": [],
				"contact": "",
				"mobile": ""
			}
			return {
				// 状态栏高度
				statusBarHeight: 0,
				// 导航栏高度
				navBarHeight: 82 + 11,
				formData,
				formOptions: {},
				rules: {
					...getValidator(Object.keys(formData))
				},
				pattern: {
					icon: "list"
				}
			}
		},
		onReady() {
			this.$refs.form.setRules(this.rules)
		},
		created() {
			//获取手机状态栏高度
			this.statusBarHeight = uni.getSystemInfoSync()['statusBarHeight'];
		},
		methods: {
			/**
			 * 触发表单提交
			 */
			submit() {
				uni.showLoading({
					mask: true
				})
				this.$refs.form.validate().then((res) => {
					this.submitForm(res)
				}).catch(() => {
					uni.hideLoading()
				})
			},

			submitForm(value) {
				// 使用 clientDB 提交数据
				db.collection(dbCollectionName).add(value).then((res) => {
					uni.showToast({
						icon: 'none',
						title: '提交成功'
					})
					this.getOpenerEventChannel().emit('refreshData')
					setTimeout(() => uni.navigateBack(), 500)
				}).catch((err) => {
					uni.showModal({
						content: err.message || '请求服务失败',
						showCancel: false
					})
				}).finally(() => {
					uni.hideLoading()
				})
			},

			fabClick() {
				uni.navigateTo({
					url: "./list"
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.uni-container {
		padding: 15px;

		.navBarBox {
			.statusBar {}

			.navBar {
				padding: 3rpx 50rpx;
				padding-bottom: 8rpx;
				display: flex;
				flex-direction: row;
				justify-content: center;
				align-items: center;

				.logo {
					width: 82rpx;
					height: 82rpx;
					margin-right: 10rpx;
					filter: invert(1);
				}
			}
		}
	}

	.uni-input-border,
	.uni-textarea-border {
		width: 100%;
		font-size: 14px;
		color: #666;
		border: 1px #e5e5e5 solid;
		border-radius: 5px;
		box-sizing: border-box;
	}

	.uni-input-border {
		padding: 0 10px;
		height: 35px;

	}

	.uni-textarea-border {
		padding: 10px;
		height: 80px;
	}

	.uni-button-group {
		margin-top: 50px;
		/* #ifndef APP-NVUE */
		display: flex;
		/* #endif */
		justify-content: center;
	}

	.uni-button {
		width: 184px;
		padding: 12px 20px;
		font-size: 14px;
		border-radius: 4px;
		line-height: 1;
		margin: 0;
		background-color: #fa3534;
	}
</style>